export const initialState = {
    loading: true,
    movies: [],
    errorMessage: null
}

export const reducer = (state, action) => {
    switch (action.type) {
        case 'SEARCH_MOVIES_REQUEST':
            return {
               ...state,
               errorMessage: null,
               loading: true
            };
        case 'SEARCH_MOVIES_SUCCESS':
            return {
                ...state,
                loading: false,
                movies: action.payload
            };
        case 'SEARCH_MOVIES_FAIL':
            return {
                ...state,
                loading: false,
                errorMessage: action.error
            };
        default:
            return state;
    }
}